.ourStory {
  background-color: $color-secondary;
  ::selection {
    color: $color-secondary;
    background-color: $color-grey-dark-2;
  }
  &__centered {
    text-align: center;
  }
  &__head {
    width: 30rem;
    height: auto;
    display: inline-block;
    padding-top: 4rem;
    padding-bottom: 2rem;
    @media screen and (max-width: 570px) {
      padding-top: 15rem;
    }
  }
  &__icon {
    height: 6rem;
    width: auto;
    display: inline-block;
    margin-bottom: 2rem;
  }
  p {
    font-weight: 400;
    font-size: 2.7rem;
    padding: 0 35rem;
    margin-bottom: 2rem;
    @media screen and (max-width: 600px) {
      font-size: 1.9rem;
      padding: 0 1rem;
    }
    a {
      text-decoration: none;
      color: inherit;
      border-bottom: 3px solid;
      &:active,
      :visited {
        color: inherit;
      }
    }
  }
  .icon {
    text-align: center;
  }
}
